
const DEFAULT_WIDTH = 460;
const DEFAULT_HEIGHT = 418;

const GRID_LINES = 7;

export default class Chart {
    constructor({
      id,
      width = DEFAULT_WIDTH,
      height = DEFAULT_HEIGHT,
      data,
    }, parentElement) {
      const { columns, colors, names, types } = data;
      const props = {
        id,
        width,
        height,
        parentElement,
        columns,
        colors,
        names,
        types,
      };
      this.props = props;
    }

    init() {
      const { id, parentElement, width, height } = this.props; 
      if (!id || !parentElement) {
          return false;
      }

      const element = document.createElement('canvas');
      element.setAttribute('id', id);
      if (width) {
        element.width = width;
      }
      if (height) {
        element.height = height;
      }
      if (element.getContext) {
        const ctx = element.getContext('2d');
        this.ctx = ctx;
        this.element = element;
        parentElement.appendChild(element);
        
        this.render();
      } else {
        // canvas-unsupported code here
      }
    }

    render() {
      this.renderGrid();
      this.renderBottomLabels();
      this.renderLines();
    }

    renderGrid() {
      const ctx = this.ctx;

      const sizeWidth = ctx.canvas.clientWidth;
      const sizeHeight = ctx.canvas.clientHeight;
      const distance = (sizeHeight - 40) / GRID_LINES;
      ctx.beginPath();
      for (let i = 1; i <= GRID_LINES; ++i) {
        const pos = Math.floor(i * distance) + 0.5;
        ctx.moveTo(0, pos); 
        ctx.lineTo(sizeWidth, pos);
      }
      ctx.strokeStyle = 'lightgray';
      ctx.lineWidth = 1;
      ctx.stroke();
    }

    renderBottomLabels() {
      const { columns } = this.props;
      let [ xPoints ] = columns;
      [, ...xPoints] = xPoints; 
      const ctx = this.ctx;

      const sizeWidth = ctx.canvas.clientWidth;
      const X_DISTANCE = 100;
      const count = sizeWidth / X_DISTANCE;

      const points = xPoints.length;
      const pointsDistance = Math.floor(points / count);
      for (let i = pointsDistance; i < xPoints.length - 1; i += pointsDistance) {
        const time = xPoints[i];
        console.log(new Date(time));
      }
    }

    renderLines() {
      const ctx = this.ctx;
      const { columns } = this.props;
      const [ xPoints, ...yPoints ] = columns;
      ctx.beginPath();
      ctx.moveTo(0, 0);
      yPoints.forEach((y, i) => {
        if (!i) {
          return;
        }
        const x = xPoints[i];
        ctx.lineTo(x, y);
      });
      ctx.strokeStyle = 'red';
      ctx.stroke();
    }
};